.App {
  text-align: left;
  padding: 30px;
}

.note {
  color: grey;
  padding-top: 5px;
  font-size: 15px;
}
h1, h2 {
  color: green;
  font-style: italic;
}

.info {
  color: green;
}

.error {
  color: red;
}

.info,
.error {
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}

